@import "../../global.scss";

.presentation {
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-around;

    @include mobile {
        padding-top: 50px;
        flex-direction: column;
    }

    .left {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        @include mobile {
        }

        h1 {
            font-size: 40px;
            margin-top: 20px;

            @include mobile {
                font-size: 30px;
            }
        }

        .imgContainer {
            width: 450px;
            height: 450px;
            overflow: hidden;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;

            @include mobile {
                width: 250px;
                height: 250px;
            }

            img {
                width: 100%;
            }
        }
    }

    .right {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        .message {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-right: 20%;

            @include mobile {
                margin-right: 0;
            }

            h1 {
                font-size: 60px;

                @include mobile {
                    font-size: 25px;
                }
            }

            h3 {
                font-size: 18px;

                @include mobile {
                    text-align: center;
                }
            }
        }

        .cv-btn {
            margin-top: 10%;
            margin-left: 30%;
            border: solid 2px #2c3e50;
            background-color: transparent;
            width: 200px;
            height: 40px;
            border-radius: 10px;
            text-decoration: none;
            color: #2c3e50;
            box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.152);
            transition: all 0.5s ease;

            @include mobile {
                width: 200px;
                height: 40px;
                margin-left: 0;
            }

            &:hover {
                background-color: black;
                color: white;
                border: none;
                box-shadow: 0px 5px 5px 2px rgba(28, 28, 28, 0.371);
            }
        }
    }
}
