@import "../../global.scss";

.projets {
    background-color: #03a9f4;
    padding-top: 5%;

    .top {
        padding-left: 100px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        gap: 20px;

        @include mobile {
            flex-direction: column;
            padding-left: 0;
            padding-top: 20px;
            gap: 0;
        }

        .formation {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;

            @include mobile {
                align-items: center;
            }
            h1 {
                color: white;
            }

            h3 {
                color: #2c3e50;

                @include mobile {
                    text-align: center;
                }
            }
        }

        .etna-pic {
            width: 200px;

            @include mobile {
                margin-bottom: 30px;
            }
        }
    }

    .projects {
        list-style: none;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        gap: 40px;
        padding-top: 90px;

        .project-card {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            width: 400px;
            height: 200px;
            padding: 20px;
            border-radius: 20px;
            background-color: white;
            color: #2c3e50;
            box-shadow: 0px 5px 5px 2px #0d94d3;
            transition: all 0.5s ease;
            overflow: hidden;
            padding: 20px;
            cursor: pointer;

            @include mobile {
                width: 150px;
                height: 150px;
                padding: 0;
            }

            .imgContainer {
                background-color: black;
                border-radius: 10px;
                width: 300px;
                transition: all 0.5s ease;
                z-index: 1;
                overflow: hidden;

                img {
                    background-color: black;
                    border-radius: 10px;
                    width: 300px;
                    transition: all 0.5s ease;
                    z-index: 1;

                    @include mobile {
                        width: 100%;
                    }
                }
            }

            &:hover {
                width: 410px;
                height: 210px;
                background-color: white;
                box-shadow: 0px 5px 5px 2px #0d94d3b1;
                color: black;

                @include mobile {
                    width: 160px;
                    height: 160px;
                    padding: 0;
                }
            }

            h1 {
                display: block;

                @include mobile {
                    display: none;
                }
            }
        }
    }
}
