@import "../../global.scss";

.intro {
  background-image: url(../../../public/assets/backGround.jpeg);
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  text-shadow: 0px 5px 5px 2px rgba(65, 65, 65, 0.123);

  .downArrow {
    width: 50px;
    position: absolute;
    left: 50%;
    animation: float 2s linear infinite;
    transition: all 0.5s ease;
    border-radius: 50px;

    &:hover {
      transform: rotate(720deg);
      background-color: #03a9f4;
    }
  }
  @keyframes float {
    0% {
      bottom: 20px;
      opacity: 100%;
    }
    50% {
      bottom: 30px;
      opacity: 10%;
    }
    100% {
      bottom: 20px;
      opacity: 200%;
    }
  }

  .content {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h1 {
      font-size: 60px;
      font-weight: 400;
      font-family: none;
      color: transparent;
      -webkit-text-stroke: 2px #03a9f4;
    }

    h3 {
      font-size: 60px;
      font-weight: 300;
      text-align: center;
  
      @include mobile {
        font-size: 30px;
      }
    }
  }

  @keyframes animate {
    100% {
      clip-path: polygon(0% 45%, 16% 44%, 33% 50%, 54% 60%, 70% 61%, 84% 59%, 100% 52%, 100% 100%, 0% 100%);
    }

    50% {
      clip-path: polygon(0% 60%, 15% 65%, 34% 66%, 51% 62%, 67% 50%, 84% 45%, 100% 46%, 100% 100%, 0% 100%);
    }
  }

  .post {
    text-transform: uppercase;
    letter-spacing: 2px;
    background: linear-gradient(45deg, #03a9f4, #fff, #000);
    background-size: 60%;
    animation: anim 5s ease infinite;
    -webkit-background-clip: text;
    -webkit-text-fill-color: rgba(255, 255, 255, 0);
  }

  @keyframes anim {
    0% {
      background-position: -400%;
    }
    50% {
      background-position: 100%;
    }
    100% {
      background-position: -400%;
    }
  }
}
