@import "../../global.scss";

.competences {
    background-color: yellow;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .top {
        background-color: white;

        .A-B-C {
            display: flex;
            color: #333;
            font-family: tahoma;
            font-size: 3rem;
            font-weight: 100;
            line-height: 1.5;
            text-transform: uppercase;
            white-space: nowrap;
            overflow: hidden;
            position: relative;
            width: 100%;

            @include mobile {
                display: none;
            }

        }

        .A-B-C span {
            margin-left: 20px;
            font-size: 40px;
        }

        .message {
            background-color: yellow;
            display: block;
            font-weight: 900;
            overflow: hidden;
            position: absolute;
            left: 250px;
            animation: openclose 5s ease infinite;
        }

        .word1,
        .word2,
        .word3 {
            font-family: tahoma;
        }

        @keyframes openclose {
            0% {
                top: 0.2rem;
                width: 0;
            }
            5% {
                width: 0;
            }
            15% {
                width: 230px;
            }
            30% {
                top: 0.2rem;
                width: 300px;
            }
            33% {
                top: 0.2rem;
                width: 0;
            }
            35% {
                top: 0.2rem;
                width: 0;
            }
            38% {
                top: -4.5rem;
            }
            48% {
                top: -4.5rem;
                width: 190px;
            }
            62% {
                top: -4.5rem;
                width: 250px;
            }
            66% {
                top: -4.5rem;
                width: 0;
                text-indent: 0;
            }
            71% {
                top: -9rem;
                width: 0;
                text-indent: 5px;
            }
            86% {
                top: -9rem;
                width: 285px;
            }
            95% {
                top: -9rem;
                width: 285px;
            }
            98% {
                top: -9rem;
                width: 0;
                text-indent: 5px;
            }
            100% {
                top: 0;
                width: 0;
                text-indent: 0;
            }
        }
    }
    .middle {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        justify-content: space-around;
        align-items: center;

        @include mobile {
            flex-direction: column-reverse;
            justify-content: space-between;
            height: 100%;
            padding-bottom: 30px;
        }

        .left {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 50px;

            @include mobile {
                gap: 10px;
            }

            .langsContainer {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;

                @include mobile {
                    width: 60%;
                    flex-direction: column-reverse;
                }
                
                .langs {
                    height: 240px;
                    width: 300px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-wrap: wrap;
                    row-gap: 20px;
                    column-gap: 20px;

                    @include mobile {
                        row-gap: 0;
                        column-gap: 20;
                        height: 100px;
                    }
                }
                .lang-card {
                    width: 100px;
                    height: 100px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    box-shadow: 0px 5px 5px 2px rgba(65, 65, 65, 0.123);
                    border-radius: 10px;
                    transition: all 0.2s ease;

                    @include mobile {
                        height: 50px;
                        width: 50px;
                    }

                    &:hover {
                        background-color: white;
                    }

                    .lang-logo {
                        height: 70px;

                        @include mobile {
                            height: 20px;
                        }
                    }
                }
            }

            .frameworksContainer {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;

                @include mobile {
                    flex-direction: column-reverse;
                }

                .frameworks {
                    height: 100px;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-wrap: wrap;
                    row-gap: 20px;
                    column-gap: 20px;
                }

                .framework1-card {
                    width: 100px;
                    height: 100px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    box-shadow: 0px 5px 5px 2px rgba(65, 65, 65, 0.123);
                    border-radius: 10px;
                    transition: all 0.2s ease;

                    @include mobile {
                        width: 50px;
                        height: 50px;
                    }

                    &:hover {
                        background-color: #fb503b;
                        width: 105px;
                        height: 105px;
                    }

                    .framework-logo {
                        height: 70px;

                        @include mobile {
                            height: 20px;
                        }
                    }
                }

                .framework2-card {
                    width: 100px;
                    height: 100px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    box-shadow: 0px 5px 5px 2px rgba(65, 65, 65, 0.123);
                    border-radius: 10px;
                    transition: all 0.2s ease;

                    @include mobile {
                        width: 50px;
                        height: 50px;
                    }

                    &:hover {
                        background-color: #61DBFB;
                        width: 105px;
                        height: 105px;
                    }

                    .framework-logo {
                        height: 70px;

                        @include mobile {
                            height: 20px;
                        }
                    }
                }

                .framework3-card {
                    width: 100px;
                    height: 100px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    box-shadow: 0px 5px 5px 2px rgba(65, 65, 65, 0.123);
                    border-radius: 10px;
                    transition: all 0.2s ease;

                    @include mobile {
                        width: 50px;
                        height: 50px;
                    }

                    &:hover {
                        background-color: #42b883;
                        width: 105px;
                        height: 105px;
                    }

                    .framework-logo {
                        height: 70px;

                        @include mobile {
                            height: 20px;
                        }
                    }
                }
            }
        }

        .right {
            .coding-img {
                width: 350px;

                @include mobile {
                    width: 200px;
                    margin-top: 200px;
                }
            }
        }
    }
    .down {
        transform: rotate(-180deg);
        background-color: white;
        .A-B-C {
            display: flex;
            color: #333;
            font-family: tahoma;
            font-size: 3rem;
            font-weight: 100;
            line-height: 1.5;
            text-transform: uppercase;
            white-space: nowrap;
            overflow: hidden;
            position: relative;
            width: 100%;
            
            @include mobile {
                display: none;
            }
        }

        .A-B-C span {
            margin-left: 20px;
            font-size: 40px;
        }

        .message {
            background-color: yellow;
            display: block;
            font-weight: 900;
            overflow: hidden;
            position: absolute;
            left: 250px;
            animation: openclose 5s ease infinite;
        }

        .word1,
        .word2,
        .word3 {
            font-family: tahoma;
        }

        @keyframes openclose {
            0% {
                top: 0.2rem;
                width: 0;
            }
            5% {
                width: 0;
            }
            15% {
                width: 230px;
            }
            30% {
                top: 0.2rem;
                width: 300px;
            }
            33% {
                top: 0.2rem;
                width: 0;
            }
            35% {
                top: 0.2rem;
                width: 0;
            }
            38% {
                top: -4.5rem;
            }
            48% {
                top: -4.5rem;
                width: 190px;
            }
            62% {
                top: -4.5rem;
                width: 250px;
            }
            66% {
                top: -4.5rem;
                width: 0;
                text-indent: 0;
            }
            71% {
                top: -9rem;
                width: 0;
                text-indent: 5px;
            }
            86% {
                top: -9rem;
                width: 285px;
            }
            95% {
                top: -9rem;
                width: 285px;
            }
            98% {
                top: -9rem;
                width: 0;
                text-indent: 5px;
            }
            100% {
                top: 0;
                width: 0;
                text-indent: 0;
            }
        }
    }
}
