.nav {
    background-color: white;
    height: 250px;
    width: 50px;
    border-radius: 50px;
    position: fixed;
    top: 40%;
    left: -45px;
    z-index: 1;
    transition: all 0.5s ease;
    opacity: 20%;
    box-shadow: 0px 5px 5px 2px rgba(65, 65, 65, 0.123);

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

    &:hover {
        opacity: 100%;
        transform: translateX(45px);
    }

    .nav-btn {
        width: 30px;
        transition: all 0.2s ease;
        
        &:hover {
            width: 35px;
        }
    }
}