@import "../../global.scss";

.contact {
    background-color: #000;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      justify-content: center;
    }

    .message {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 24%;
        gap: 30px;

        @include mobile {
          margin-top: 200px;
        }

        h2 {
            font-size: 40px;
            text-transform: uppercase;
            letter-spacing: 2px;
            background: linear-gradient(45deg, yellow, #fff, #000);
            background-size: 40%;
            animation: anim 5s ease infinite;
            -webkit-background-clip: text;
            -webkit-text-fill-color: rgba(255, 255, 255, 0);

            @include mobile {
              padding: 10px;
              font-size: 20px;
              text-align: center;
            }

            @keyframes anim {
                0% {
                  background-position: -200%;
                }
                50% {
                  background-position: 500%;
                }
                100% {
                  background-position: -200%;
                }
              }
        }
    }

    a {
      color: white;
      transition: all 0.2s ease;

      @include mobile {
        font-size: 10px;
      }
      
      &:hover {
        color: yellow;
      }
    }

    h4 {
        color: #9aa4af; 
        margin-bottom: 10px;

        @include mobile {
          font-size: 10px;
          margin-top: 200px;
        }
    }
}
